'use strict';
import Message from 'antd/lib/message';
/**
 * [https://github.com/github/fetch，下载该文件进行修改的，该库竟然不支持cmd格式]
 * @param  {[type]} url     [description]
 * @param  {Object} options [description]
 * @return {[type]}         [description]
 */
let self = function fetch(url, options = {}) {
    let opt = Object.assign({
        method: 'GET',
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        }
    }, options);
    if (opt.method.toLowerCase() === 'get' && opt.body) {
        const body = opt.body;
        const query = Object.keys(body).map(key => {
            return `${key}=${body[key]}`;
        });
        if (url.indexOf('?') != -1) {
            url += '&';
        } else {
            url += '?';
        }
        url += query.join('&');
        delete opt.body;
    }
    // post put 的时候，需要处理一下 body
    if (typeof opt.body === 'object') {
        opt.body = JSON.stringify(opt.body);
    }

    const res = self.fetch(url, opt)
        .then(response => {
            if (response.status >= 200 && response.status < 300) {
                return response
            } else {
                Message.error('服务器忙晕了，请稍后再试');
                var error = new Error('服务器忙晕了，请稍后再试')
                error.response = response
                throw error
            }
        })
        .then(response => response.json())
        .then(response => {
            if (response.code != 200) {
                Message.error(response.message || '服务器忙晕了，请稍后再试');
                var error = new Error(response.message || '服务器忙晕了，请稍后再试')
                error.response = response
                throw error
            }
            return response;
        });
    return res;
};


